@import url('https://fonts.googleapis.com/css2?family=Raleway&family=Grand+Hotel&family=Roboto+Slab:wght@400;700&display=swap');
@import 'resets';
@import 'variables';
@import 'mixins';
@import 'text';
@import 'header';
@import 'nav';
@import 'containers';
@import 'about';
@import 'profile';
@import 'features';
@import 'contribute';
@import 'sourcefiles';
@import 'footer';

body {
  background-color: $background-color;
  font-family: $body-font;
  font-size: 16px;
  line-height: 24px; }

main, header {
  width: calc(100vw - 2rem);
  margin: 0 auto;

  @media (min-width: 800px) {
    max-width: 800px;
    margin: 0 auto; } }

a.link-guidelines {
  @include drop-shadow($lighthighlight-color);
  max-width: 50%;
  margin: 0 auto;
  text-align: center;

  @media (min-width: 768px) {
    width: calc(100% - 2rem); } }


a.link-allstyles {
  width: 10rem; }
