@import 'variables';
@import 'mixins';

footer.page-footer {
  background-color: $darkaccent-color;
  color: $lighthighlight-color;
  padding: 1rem;
  text-align: center;
  max-width: 100%;

  a {
    color: $lighthighlight-color; }


  p {
    max-width: 800px;
    margin: 0 auto;

    a {
      border-bottom: dashed 1px $lighthighlight-color; } }



  .container ul {
    display: grid;
    grid-template-columns: repeat(auto-fit, calc(50% - 1rem));
    gap: 1rem;
    max-width: 800px;
    background-color: transparent;
    margin: 2rem auto;
    padding: 0;

    li a {
      @include drop-shadow($darkaccent-color);
      height: auto;

      svg {
        fill: $lighthighlight-color; } } } }
